.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.item_container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    justify-content: center;
}

.item {
    font-size: 12px;
    border: 1px solid black;
    margin-right: 5%;
    margin-bottom: 5%;
    width: 20%;
    padding: 10px;
    word-wrap: break-word;
}

a {
    text-decoration: none;  /* Убирает подчеркивание */
    color: inherit;         /* Наследует цвет от родителя (в данном случае цвет h1) */
}

.spinner {
    border: 4px solid #f3f3f3; /* Светлый фон */
    border-top: 4px solid #3498db; /* Синий цвет */
    border-radius: 50%;
    width: 50px;
    height: 50px;
    animation: spin 2s linear infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}